body {
  font-family: "Century Gothic", 'Lato', sans-serif;
  overflow: hidden;
}

a {
text-decoration: none;
font-size: 3vw;
color: rgba(135, 206, 250, 0.562);
&:hover {
  text-decoration: underline;
  }
}

c {
  font-size: 3vw;
}

.nav-bar-tabs,
.nav-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
  background-image: url('background.png');
  background-size: cover;
  text-align: center;
  padding: 0 2em;
  h1 {
      -webkit-text-stroke: .2vw black;
      font-size: 7vw;
      margin: 0;
      letter-spacing: 0;
      font-weight: bold;
      color: rgb(94, 134, 94);
  }
  h2 {
      background-color: rgba(0, 0, 0, 0.7);
      width: 70vw;
      border-color: #000;
      border-radius: 25px;
      font-size: 4vw;
      letter-spacing: 0;
      font-weight: bold;
      color: rgb(82, 57, 92);
  }
}

.nav-bar-tabs-container {
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 0;
  width: 100%;
  height: 10vh;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  background: #666;
  z-index: 10;
  &--top {
      position: fixed;
      top: 0;
  }
}

.nav-bar-tab {
  text-decoration: none;
  -webkit-text-stroke: .05vw #666;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 3vw;
  flex: 1;
  color: #000;
  letter-spacing: 0.1rem;
  transition: all 0.5s ease;
  &:hover {
    -webkit-text-stroke: .05vw black;
    color:rgb(94, 134, 94);
    background: rgb(0, 0, 0);
    transition: all 0.5s ease;
  }
}

.nav-bar-tab-slider {
  position: absolute;
  bottom: 0;
  width: 0;
  height: 1.2vh;
  background: linear-gradient(#666, black);
  transition: left 0.3s ease;
}

.link-container{
  width: 50vw;
  justify-content: space-around;
  display: flex;
  flex-direction: row;
}

.mlink-container{
  width: 20vw;
  justify-content: space-around;
  display: flex;
  flex-direction: row;
}

.resumelink{
  &:hover {
    text-decoration: underline;
    }
}

.fa {
  padding: 1vw;
  font-size: 3vw;
  width: 3.5vw;
  text-align: center;
  text-decoration: none;
  border-radius: 20%;
}

.fa-facebook {
  background: #3B5998;
  color: white;
}

.fa-linkedin {
  background: #0077B5;
  color: white;
}

.fa-google {
  background: #ea4335;
  color: white;
}

.fa-github {
  background: #00405d;
  color: white;
}

.fa-soundcloud {
  background: #ff7700;
  color: white;
}

.fa-youtube {
  background: #cd201f;
  color: white;
}

.fa:hover {
  opacity: 0.7;
}

@media (min-width: 800px) {
.nav-bar-tabs,
.nav-slide {
  h1 {
      font-size: 7vw;
  }
  h2 {
      font-size: 4vw
  }
}
.nav-bar-tab {
  font-size: 3vw;
}
}